// Module
___CSS_LOADER_EXPORT___.push([module.id, "#map[data-v-43385f0a]{height:450px}", "",{"version":3,"sources":["webpack://./resources/js/components/UserActivityOnMap.vue"],"names":[],"mappings":"AAwDA,sBACA,YACA","sourcesContent":["<template>\n    <div id=\"map\" ref=\"Map\"></div>\n</template>\n\n<script>\n\nimport { Loader } from \"@googlemaps/js-api-loader\"\nconst loader = new Loader({\n    apiKey: process.env.MIX_GOOGLE_MAP_API_KEY,\n    version: \"weekly\",\n});\n\nexport default {\n    name: \"UserActivityOnMap\",\n    data() {\n        return {\n            map: null\n        }\n    },\n    props: {\n        activities: {\n            type: Array,\n            default(){\n                return []\n            }\n        },\n    },\n    mounted() {\n\n        loader.load().then(() => {\n           this.map = new google.maps.Map(document.getElementById(\"map\"), {\n                center: { lat: 51.508742, lng:  -0.120850},\n                zoom: 8,\n            });\n            var bounds = new google.maps.LatLngBounds();\n\n            for (const i in this.activities) {\n                if (this.activities[i].location){\n                    let marker = new google.maps.Marker({\n                        position:{ lat: parseFloat(this.activities[i].location.latitude), lng: parseFloat(this.activities[i].location.longitude) },\n                        map: this.map,\n                    });\n\n                    bounds.extend(marker.position);\n                }\n            }\n\n            this.map.fitBounds(bounds);\n        });\n    }\n\n\n}\n</script>\n\n<style scoped>\n#map {\n    height: 450px;\n}\n</style>\n"],"sourceRoot":""}]);
